@font-face {
  font-family: "Eurostile";
  src: url("./Components/Fonts/eurostile.woff") format("woff"),
    url("./Components/Fonts/EuroStyle\ Normal.woff") format("woff");
}
.fl-C {
  display: flex;
  align-items: center;
  justify-content: center;
}
.fl-Column{
  flex-direction: column;
}
.mtb-1 {
  margin-top: 4rem!important;
  margin-bottom: 4rem!important;
}
body {
  font-family: "Eurostile", sans-serif;
}

// Navbart css -Start- //

.layout_web {
  max-width: 1400px;
  margin: auto;
  padding: 0 21px;
}

.flex-layout {
  display: flex;
  margin-top: 1.6rem;
  .logo-align,
  .right-content {
    flex-basis: 50%;
  }

  .rgt-innerContent {
    justify-content: flex-end;

    .second-btn a {
      text-decoration: none;
      /* border-width: 2px; */
      font-size: 18px;
      padding: 1.2rem;
      border: 2px solid;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      border-color: #6e07f3;
      color: #6e07f3;
      border-radius: 27px;
      height: 0.7rem;
      font-family: "europa", sans-serif;
      transition: all 0.3s ease-in-out;
    }
    :hover {
      background-color: #6e07f3;
      color: white !important;
      border-radius: 45px;
      cursor: pointer;
    }
  }
}
// Navbart css -End- //

// Section One  Css -Start- //
.name{
  height: 1368px;
}
.section-one {
  margin-top: 2rem;
  flex-direction: column;

  .first-head h1 {
    font-size: 50px;
    font-weight: 800;
    margin-bottom: 12px;
    line-height: 54px;
  }
  .second-head h2 {
    font-size: 24px;
    font-weight: 600;
    color: #464545;
  }
  .sectionOne-image {
    .sectionOne-firstImage {
      max-width: 210px;
      margin: 4rem auto;
      img {
        width: 100%;
      }
    }
    .sectionOne-innerImage-One {
      max-width: 800px;
      border-bottom: 2px solid black;
      img {
        width: 100%;
        margin: auto;
      }
    }
  }
}

// Section One Css -End- //

// Section Two Css -Start- //

.bg-color {
  background-color: #6e07f3;
  color: white;
  height: 514px;
  padding-top: 7rem;
}

.SectionTwo {
  padding-bottom: 7rem;
  .info-me {
    flex-direction: column;
    h1 {
      margin: 1rem 0;
    }
    p {
      font-size: 22px;
      line-height: 1.3;
      max-width: 800px;
      text-align: center;
    }
  }
}

// Section Two Css -End- //

// Section Three Css -Start- //

.sectionThree {
  background-color: white;
  color: black;
  border-radius: 2rem;
  height: -moz-fit-content;
  height: fit-content;
  width: fit-content;
  height: fit-content;
  margin: auto;

  .sectionThree-coulmn.fl-C {
    
    .icon {
      max-width: 130px;
      img {
        width: 100%;
      }
    }
  }

  .coulmn-Content {
    padding: 1rem 3.9rem;
    padding-top:3rem ;
    border: 1px solid #cccbcb;
    flex-direction: column;
    text-align: center;
    border-radius: 2rem;
box-shadow: -4px 23px 31px -12px rgba(0,0,0,0.75);

    h3 {
      font-size: 24px;
      margin-bottom: 13px;
    }
    p {
      font-size: 18px;
    }
    .deitals {
      font-size: 18px;
      font-weight: 200;
      font-family: "europa", sans-serif;
      max-width: 350px;
      line-height: 27px;
    }
    .color {
      color: #6e07f3;
      font-size: 18px;
      margin-top: 2rem;
      margin-bottom: -7px;
    }
    .expert {
      font-size: 18px;
      font-weight: 200;
      font-family: "europa", sans-serif;
      color: black;
    }
    h4 {
      color: #6e07f3;
      font-size: 18px;
      ul {
        padding: 0px;
        li {
          list-style: none;
          font-size: 18px;
          font-weight: 200;
          color: black;
          line-height: 30px;
          font-family: "europa", sans-serif;
        }
      }
    }
  }
}


// Section Three Css -END- //

// Section Four Css -START-
.sectionFour-innerFirst{
text-align: center;
span{
  font-family: 'europa';
  font-size: 1rem;
font-weight: 400;
}
}
.sectionFour-Box {
  position: relative;
 
  :hover{
    cursor: pointer;

  }
  figure {
  max-width: 450px;
 height: 288px;
  margin: auto;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;
  }
figcaption{
  color: white;
  font-size: larger;
}
  .hiddenSectionFour{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 33%;
    width: 100%;
    
    opacity: 0;
    a{
      padding: 0.9em 1.5em;
      font-size: 1.2rem;
      font-weight: 400;
      border: 2px solid #6e07f3;
      margin-top: 2rem;
      border-radius: 69px;
      background-color: transparent;
      color: white;
      text-decoration: none;
      width: -moz-fit-content;
      width: fit-content;
      transition:all .2s ease-in-out
    }
    
    a:hover{
      background-color: #6e07f3;
      border:2px solid transparent;
    }
  }
}

}




































// Start Media Query For Layout //

@media screen and (max-width: 1484px) and (min-width: 1000px) {
  .layout_web {
    max-width: 1000px;
  }
}

@media screen and (max-width: 1000px) and (min-width: 750px) {
  .layout_web {
    max-width: 750px;
  }
}

@media screen and (max-width:666px) {
  .bg-color,.name{
    height: auto;
  }
  .sectionThree .coulmn-Content{
    padding: 1rem 1.1rem;
  }
}
@media screen and (max-width:438px) {
  .sectionFour-Box figure .hiddenSectionFour a{
    padding: 0.9em 1.2em;
    font-size: 1rem;
    margin-top: 0.9rem;
  }
  .sectionFour-Box figure figcaption{
    font-size: 100%;
    text-align: center;
    padding: 0 12px;
  }
  .sectionFour-Box figure .hiddenSectionFour{
    top: 25%;
   
  }
}

@media screen and (max-width:520px) {
  .bg-color,.name{
    height: auto;
  }
  .SectionTwo .info-me h1 , .SectionTwo .info-me p{
text-align: left;
}
.sectionFour-Box figure{
  height: auto;
}
.sectionFour-Box figure img{
  object-fit: contain;
}
}